
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { resetPwd } from '@/api/user'
export default {
name: 'editPassword',
components: {
XInput,
Group,
XButton
},
data () {
return {
repwd: '',
postData: {
pwd: ''
}
}
},
methods: {
submitReset () {

if (this.postData.pwd !== this.repwd) {
this.$vux.toast.text('两次输入不一致！')
return
}
resetPwd(this.postData)
.then(res => {
if (res.data.errorcode === 0) {

this.$vux.toast.text('修改成功', 'middle')


this.$router.replace({path: '/usercenter'})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
}
}



